// VARIABLES
//■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀
$primary: #dd5555;
$background: #ebebeb;
$navbar-background: #DDE236;
$navbar-front: #91AD37;
$navbar-background-focus: #A8C64D;
$space: 1rem;
$factor_font_size: 16;

//■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀
// CSS RESET
//■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀
@font-face {
    font-family: lato;
    src: url('../fonts/lato/Lato-Regular.woff');
}
@font-face {
    font-family: roboto_slab;
    src: url('../fonts/Roboto_Slab/RobotoSlab-Regular.ttf');
}

*,
*:after,
*:before {
    margin: 0;
    padding: 0;
    border: 0;
    //outline: 0;
    -webkit-tap-highlight-color: transparent;
    box-sizing: border-box;
    font-size: 1rem;
    line-height: 1;
    font: inherit;
}

footer,
header,
main,
nav {
    display: block;
}

html {
    font-family: 'roboto_slab', 'lato', 'Open Sans', sans-serif;
    font-size: 16px;
    /*font-family: 'Montserrat', sans-serif;*/
}
/**:not(input):not(textarea) {
  -webkit-user-select: none; // disable selection/Copy of UIWebView
  -webkit-touch-callout: none; // disable the IOS popup when long-press on a link
}*/
body {
    background-color: #FFF;
}

img {
    display: inline-block;
    max-width: 100%;
}

input:not([type=checkbox]):not([type=radio]) {
    -webkit-appearance: none;
}

button {
    background: none;
    cursor: pointer;
}

form {
    width: 100%;
    height: 100%;
}

b {
    font-weight: 600;
}

a {
    text-decoration: none;
    color: inherit;
}

p {
    margin-bottom: $space/2;
}

.aria-only-text {
    position: absolute;
    left: -10000px;
    top: auto;
    width: 1px;
    height: 1px;
    overflow: hidden;
}
//■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀
// IMPORT
//■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀■▀

@import "btn";
@import "product";

*,
*:after,
*:before {
    box-sizing: border-box;
}

img {
    max-width: 100%;
    height: auto;
    vertical-align: sub;
}

body {
    background: $background;
}

.box {
    background: #f9f9f9;
    box-shadow: 0 0 1px rgba(0,0,0,.2), 0 2px 4px rgba(0,0,0,.1);
    border-radius: 5px;
    margin-bottom: 20px;
    text-shadow: 1px 1px 1px rgba(0,0,0,.1);

    h2 {
        color: $primary;
        font-weight: bold;
        font-size: (23/$factor_font_size) + em;
    }

    p {
        font-size: (17/$factor_font_size) + em;
        color: #7d7d7d;
    }
}

.navbar {
    background-color: $navbar-background;
    color: $navbar-front;
    border-width: 0;
    border: 1px solid transparent;
    border-color: transparent;
    z-index: 1000;
    position: relative;
    min-height: 60px;
    margin-bottom: 21px;
}
.nav {
    padding-left: 0;
    margin-bottom: 0;
    list-style: none;
}

.nav > li > a:hover, .nav > li > a:focus {
    text-decoration: none;
    background-color: $navbar-front;
    color: white;
}

.navbar-nav {
    margin: 7.5px -15px;
}

.navbar-collapse {
    max-height: 340px;
    margin-right: -15px;
    margin-left: -15px;
}

.nav>li {
    position: relative;
    display: block;
}

ol, ul {
    margin-top: 0;
    margin-bottom: 10px;
}

@media (min-width: 768px) {
    .container {
        width: 750px;
    }

    .navbar-header {
        float: left;
    }

    .navbar > .container .navbar-brand {
        margin-left: -15px;
    }

    .navbar-collapse,.navbar-header, .container>.navbar-collapse, .container>.navbar-header {
        margin-right: 0;
        margin-left: 0;
    }

    .navbar-collapse.collapse {
        display: block!important;
        height: auto!important;
        padding-bottom: 0;
        overflow: visible!important;
        width: auto;
        border-top: 0;
        -webkit-box-shadow: none;
        box-shadow: none;
        padding-right: 0;
        padding-left: 0;
        margin-right: 0;
        margin-left: 0;
    }

    .navbar-nav {
        float: left;
        margin: 0;
    }

    .navbar-nav>li {
        float: left;
    }

    .col-sm-3, .col-sm-9 {
        float: left;
    }

    .col-sm-3 {
        width: 25%;
    }

    .col-sm-9 {
        width: 75%;
    }
}
@media (min-width: 992px) {
    .container {
        width: 970px;
    }
    .col-sm-3, .col-sm-9 {
        float: left;
    }

    .col-sm-3 {
        width: 25%;
    }

    .col-sm-9 {
        width: 75%;
    }
}
@media (min-width: 1200px) {
    .container {
        width: 1170px;
    }
    .col-sm-3, .col-sm-9 {
        float: left;
    }

    .col-sm-3 {
        width: 25%;
    }

    .col-sm-9 {
        width: 75%;
    }

}

.container {
    margin-right: auto;
    margin-left: auto;
    padding-left: 15px;
    padding-right: 15px;
}

/* Article */
.article-large {
    padding: 20px;
}

.flex {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    flex-direction: row;
}
